.header{
    width: 100%;
    background-color: #041734;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 10;
}

.header__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1500px;
    height: 90px;
}

ul.nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: fit-content;
    height: 100%;

    li.nav__item{
        width: auto;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: background .15s ease-in;
        position: relative;

        a.nav__item__link{
            color: white;
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            padding: 0 20px;
            font-size: 14px;

            .arrow{
                margin-left: 5px;

                .svg{
                    width: 8px;
                    height: 13px;

                    path{
                        fill: white;
                    }
                }

            }
        }

        &:hover{
            background: #293d4e;
            transition: background .3s;

            a.nav__item__link{
                color: #c8d0da;

                .arrow{
                    .svg{
                        path{
                            fill: #c8d0da;
                        }
                    }
                }
            }
        }
    }

    li.nav__item.sub__nav__item{
        &:hover{
            .sub__nav{
                visibility: visible;
                opacity: 1;
                transition: opacity .3s;
            }
        }
    }
}

ul.sub__nav{
    opacity: 0;
    transition: opacity .15s ease-in;
    visibility: hidden;
    position: absolute;
    top: 90px;
    left: 0;
    width: auto;
    max-width: 200px;
    min-width: 90px;

    li.nav__item{
        background: #293d4e;
        padding: 10px 0;
        justify-content: flex-start;
        width: 100%;
        align-items: none;

        a.nav__item__link{
            width: max-content;
        }

        &:hover{

            a.nav__item__link{
                color: white;
            }
            
        }
    }
}

.header__other{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 360px;
    height: 100%;
}

.select__language{
    cursor: pointer;
    position: relative;
    transition: background .15s ease-in;
    height: 100%;

    &:hover{
        background: #293d4e;
        transition: background .3s;

        .other__language{
            visibility: visible;
            opacity: 1;
            transition: opacity .3s;
        }
        
        .lang__item{
            p{
                color: #c8d0da;
            }

            .arrow{
    
                .svg{
    
                    path{
                        fill: #c8d0da;
                    }
                }
    
            }
        }
    }

    .defalt__languge{
        height: 100%;
        display: flex;
        align-self: center;
    }

    .lang__item{
        display: flex;
        align-items: center;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        color: white;
        padding: 0 20px;

        .arrow{
            margin-left: 15px;
            margin-top: 2px;

            .svg{
                width: 10px;
                height: 16.25px;

                path{
                    fill: white;
                }
            }

        }
        
        p{
            margin-left: 15px;
        }

        .lang__icon{
            width: 27px;
            height: 27px;
            border-radius: 50%;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }
        }
    }

    .other__language{
        opacity: 0;
        transition: opacity .15s ease-in;
        visibility: hidden;
        position: absolute;
        left: 0;
        top: 90px;
        width: 100%;

        .lang__item{
            background: #293d4e;
            padding: 10px 15px;
            width: 100%;

            &:hover{
                p{
                    color: white;
                }
            }
        }
    }
}

.search__btn{
    cursor: pointer;
    height: 100%;
    transition: background .15s ease-in;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover{
        background: #293d4e;
        transition: background .3s;

        svg{
            fill: #c8d0da;
        }
    }

    svg{
        width: 20px;
        height: 20px;
        fill: white;
    }
}

.burger__menu{
    display: none;
    width: 30px;
    height: 42.5px;
    cursor: pointer;

    .svg{
        fill: white;
    }
}

@media (max-width: 990px){
    .burger__menu{
        display: block;
        margin-left: 15px;
    }
    ul.nav{

        &.menu__active{
            display: flex;
        }
        display: none;
        position: fixed;
        flex-direction: column;
        background-color: #006af2;
        width: 300px;
        height: 100vh;
        top: 0;
        left: 0;
        justify-content: flex-start;
        align-items: flex-start;
        pointer-events: all;

        li.nav__item{
            width: 100%;
            height: 50px;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            cursor: pointer;
            transition: background .15s ease-in;
            position: relative;
            flex-direction: column;
            border-bottom: 1px #3a678f solid;

            &::after{
                position: absolute;
                content: '';
                top: 20px;
                right: 20px;
                width: 1px;
                height: 10px;
                background-color: #fff;
            }

            &::before{
                position: absolute;
                content: '';
                top: 24.3px;
                right: 15px;
                width: 11px;
                height: 1px;
                background-color: #fff;
            }
    
            a.nav__item__link{
                color: white;
                font-family: 'Roboto', sans-serif;
                font-weight: 400;
                height: 50px;
                display: flex;
                align-items: center;
                font-size: 16px;
    
                .arrow{
                    display: none;
    
                }
            }
    
            &:hover{
                background: none;
                transition: background .3s;
    
                a.nav__item__link{
                    color: white;
    
                    .arrow{
                        .svg{
                            path{
                                fill: #c8d0da;
                            }
                        }
                    }
                }
            }
        }

        li.nav__item.sub__nav__item{
            &:hover{
                height: fit-content;
    
                .sub__nav{
                    display: flex;
                }
            }
        }
    }

    ul.sub__nav{/* 293d4e  */
        position: static;
        top: 0;
        left: 0;
        width: 100%;
        max-width: auto;
        min-width: auto;
        display: flex;
        flex-direction: column;
        display: none;
    
        li.nav__item{
            &::after{
                display: none;
            }
            &::before{
                display: none;
            }
            border-bottom: 1px #006af2 solid;
            width: 300px;
            background: #293d4e;
            padding: 0;
            display: flex;
            align-items: center;
            height: 50px;
            align-items: none;
    
            a.nav__item__link{
                width: 100%;
            }
    
            &:hover{
    
                a.nav__item__link{
                    color: #c8d0da;
                }
                
            }
        }
    }
    
}

@media (max-width: 611px){
    .header__other{
        width: auto;

        .btn.--header{
            font-size: 13px;
            width: 130px;
            padding: 10px 0;
            border-radius: 8px;
            margin: 0 10px;
        }
    }

    .header__logo{
        img{
            width: 78.4px;
            height: 26.6px;
        }
    }

    .burger__menu{
        width: 21px;
        height: 29.75px;
    }

    .search__btn{
        padding: 0 15px;
    
    
        svg{
            width: 14px;
            height: 14px;
        }
    }

    .select__language{
        cursor: pointer;
        position: relative;
        transition: background .15s ease-in;
        height: 100%;
    
        &:hover{
            background: #293d4e;
            transition: background .3s;
    
            .other__language{
                visibility: visible;
                opacity: 1;
                transition: opacity .3s;
            }
            
            .lang__item{
                p{
                    color: #c8d0da;
                }
    
                .arrow{
        
                    .svg{
        
                        path{
                            fill: #c8d0da;
                        }
                    }
        
                }
            }
        }
    
        .defalt__languge{
            height: 100%;
            display: flex;
            align-self: center;
        }
    
        .lang__item{
            display: flex;
            align-items: center;
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            color: white;
            padding: 0 20px;
    
            .arrow{
                margin-left: 10px;
                margin-top: 2px;
    
                .svg{
                    width: 7px;
                    height: 11.375px;
    
                    path{
                        fill: white;
                    }
                }
    
            }
            
            p{
                margin-left: 10px;
            }
    
            .lang__icon{
                width: 18.9px;
                height: 18.9px;
            }
        }
    
        .other__language{
    
            .lang__item{
                background: #293d4e;
                padding: 10px;
            }
        }
    }
    
}

@media (max-width: 425px){

    ul.nav{
        width: 250px;

    }

    ul.sub__nav{/* 293d4e  */
    
        li.nav__item{
            width: 250px;
        }
    }

    .header__other{
        width: auto;

        .btn.--header{
            font-size: 11px;
            width: 100px;
            padding: 10px 0;
            border-radius: 8px;
            margin: 0 10px;
        }
    }
}

@media (max-width: 375px){
    .search__btn{
        display: none;
    }

    .select__language{
        order: 2;
    }

    .header__other{
        width: auto;

        .btn.--header{
            display: none;
        }
    }


    .header__logo{
        margin-left: 70px;
        img{
            width: calc(78.4px - 5%);
            height: calc(26.6px - 5%);
        }
    }

    .burger__menu{
        width: 17.85px;
        height: 25.2875px;
    }


}