.footer{
    background-color: #041734;
    padding: 40px 0;
}

.footer__container{
    display: flex;
    flex-direction: column;
    .btn.--footer{
        align-self: center;
        margin-top: 30px;
    }
}

.footer__main, .footer__social{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer__social{
    .footer__social__item{
        margin-left: 5px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: #1E3862;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: transform .15s ease-in;

        &:first-child{
            margin-left: 0;
        }

        .svg{
            &:nth-child(1){
                width: 50%;
                height: 50%;
            }
        }

        &:hover{
            transform: scale(1.1);
            transition: transform .3s;
        }
    }
}

.footer__policy{
    display: flex;
    align-items: center;
    margin-top: 30px;
    justify-content: center;

    a.policy{
        color: white;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 14px;
        margin-left: 30px;

        &:first-child{
            margin-left: 0;
        }
    }
}

.footer__text{
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 11px;
    color: white;
    text-align: center;
    margin-top: 30px;
    p{
        line-height: 140%;
    }

    &.--about{
        font-size: 16px;
        text-align: left;
    }
}

.footer__brands{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    .brands__item{
        margin-left: 50px;

        &:first-child{
            margin-left: 0;
        }

        transition: transform .15s ease-in;

        &:hover{
            transform: scale(1.1);
            transition: transform .3s;
        }
    }
}

@media (max-width: 683px){
    .footer__brands{
        flex-direction: column;

        .brands__item{
            margin-left: 0;
        }
    }

    .footer__main{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .footer__social{
            margin-top: 30px;
        }
    }

    .footer__container{
        .footer__main{
            order: 1;
        }

        .footer__text{
            order: 3;
        }

        .footer__text.--about{
            order: 2;
            font-size: 14px;
            text-align: center;
        }

        .footer__brands{
            order: 5;
        }

        .btn.--footer{
            order: 6;
        }

        .footer__policy{
            order: 4;
        }
    }
}

@media (max-width: 385px){
    .footer__policy{
        a.policy{
            font-size: 12px;
        }
    }
}

@media (max-width: 385px){
    .footer__policy{
        a.policy{
            font-size: 11px;
        }
    }
}