            width: 100%;
            padding: 0;
            justify-content: flex-start;
            padding: 15px;
            &:first-child{
                padding: 0;
            }

            &:nth-child(1){
                order: 3;
                margin-top: 30px;
            }
    
            &:nth-child(2){
