            }
        }
    }

    .intro__stats__about{
        padding: 0;
    }
    
}

@media (max-width: 663px){
    .swiper-button-prev-about-us,
    .swiper-button-next-about-us,
    .swiper-button-prev,
    .swiper-button-next{
        display: none;
    }

    .about__slider__swiper{
        width: 100%;
    }

    .brands__container{
        flex-direction: column;
    
    }

    .about__header{
        text-align: center;
    }
}

@media (max-width: 428px){
