.intro{
    width: 100%;
    height: 600px;
    position: relative;
    background: url('../img/intro/intro-background.jpg'), #041734;
}

.intro__about{
    width: 590px;
    padding-top: 75px;

    p.title{
        font-family: 'Roboto', sans-serif;
        font-weight: 500;
        font-size: 30px;
        color: white;
        line-height: 140%;
    }

    p.text{
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 18px;
        color: white;
        padding-top: 20px;
        line-height: 120%;
        position: relative;

        &::after{
            position: absolute;
            content: '';
            left: 0;
            bottom: -40px;
            background: #0052ff;
            width: 70px;
            height: 3px;
            clip-path: polygon(0 0, 100% 0, 95% 100%, 5% 100%);
        }
    }
}

.intro__buttons{
    padding-top: 90px;
    display: grid;
    grid-template-columns: 200px 200px;
    row-gap: 20px;
    column-gap: 30px;
    .btn.--header{
        font-size: 16px;
        width: 200px;
        padding: 15px 0;
    }
}

.intro__stats{
    width: 100%;
    padding: 30px 0;
    background-color: #041734;

    .intro__stats__container{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.intro__stats__item{
    display: flex;
    align-items: center;
    
    .is__icon{
        margin-right: 15px;
    }
    
    .is__info{

        p.title{
            color: white;
            font-size: 20px;
            font-family: 'Roboto', sans-serif;
            font-weight: 500;
        }

        p.text{
            padding-top: 5px;
            color: #C4CAD2;
            font-size: 14px;
            font-family: 'Roboto', sans-serif;
            font-weight: 500;
        }
    }
}

@media (max-width: 650px){
    .intro__about{
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;
    
        p.title{
            text-align: center;
        }
    
        p.text{
            text-align: center;
    
            &::after{
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    .intro__buttons{
        padding-top: 90px;
        display: flex;
        grid-template-columns: none;
        justify-content: center;
        flex-wrap: wrap;
        row-gap: 20px;
        column-gap: 30px;
    }

    .intro__stats{
    
        .intro__stats__container{
            display: grid;
            grid-template-columns: 1fr 1fr;
            justify-content: center;
        }
    }

    .intro__stats__item{
        display: flex;
        align-items: center;
        justify-content: flex-start;

        .is__info{

            p.title{
                font-size: 16px;
            }
    
            p.text{
                font-size: 12px;
            }
        }

        &:nth-child(3),
        &:nth-child(4){
            margin-top: 30px;
        }
    }
}

@media (max-width: 530px){

    .intro__stats{
    
        .intro__stats__container{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .intro__stats__item{
        width: 250px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4){
            margin-top: 30px;
        }
    }
}

@media (max-width: 461px){

    .intro__about{
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;

        p.title{
            font-size: 24px;
            color: white;
            line-height: 120%;
        }

        p.text{
            font-size: 16px;
        }
    }

    .intro__buttons{
        padding-top: 70px;
        display: flex;
        grid-template-columns: none;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        flex-direction: column;

        .btn.--header{
            font-size: 13px;
            width: 200px;
            padding: 15px 0;
        }
    }

    .intro__stats{
    
        .intro__stats__container{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    .intro__stats__item{
        width: 250px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4){
            margin-top: 30px;
        }
    }
}