.buy__coin{
    padding: 50px 0;
}

.buy__coin__body{
    margin-top: 50px;
    background: url('../img/intro/intro-background.jpg') center, #041734;
    padding: 40px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .btn.--header{
        font-size: 16px;
        width: 230px;
        padding: 20px 0;
        border-radius: 8px;

        &:hover{
            transform: scale(1.05);
        }
    }
}

.bcb__img{

    .bcb__img__star{
        margin-top: 40px;

        .svg{
            width: 77px;
            height: 15px;
        }
    }

}

.bcb__benefits__item{
    display: flex;
    align-items: center;
    margin-top: 10px;
    &:first-child{
        margin-top: 0;
    }
    .check{
        margin-right: 15px;
    }
    p{
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 16px;
        color: white;
    }
}

@media (max-width: 1080px){
    .bcb__img{

        .bcb__img__star{
            margin-top: 0;
        }
    
    }

    .buy__coin__body{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .btn.--header{
            margin-top: 30px;
        }
    }

    .bcb__benefits{
        margin: 0 70px;
    }
}

@media (max-width: 888px){
    .buy__coin__body{
        flex-direction: column;
        align-items: center;
    }

    .bcb__benefits{
        margin: 0;
        margin-top: 30px;
    }
}

@media (max-width: 658px){
    .buy__coin__container{
        .about__header{
            justify-content: center;
            p.title{
                text-align: center;
            }
        }
    }
    .bcb__img{
        display: flex;
        align-items: center;
        flex-direction: column;

        .bcb__img__logo{
            display: flex;
            justify-content: center;
            align-items: center;
    
            .svg{
                width: 75%;
                height: 75%;
            }
        }
    }

    .buy__coin__body .btn.--header{
        font-size: 16px;
        width: 180px;
        padding: 15px 0;
        border-radius: 8px;
    }

    .bcb__benefits__item{
        p{
            font-size: 14px;
        }
    }
}